.container {
  composes: mt-30 from global;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header {
  composes: mt-10 mb-6 text text_type_main-medium from global;
}

.button {
  composes: mt-20 from global;
}

.link {
  composes: mt-20 text text_type_main-default from global;
  color: #4c4cff;
  text-decoration: none;
}

.text {
  composes: mt-4 text text_type_main-default from global;
  color: #8585ad;
}

.parent {
  composes: mt-20 from global;
  display: grid;
  grid-template-columns: 400px 500px;
  justify-content: center;
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Aligns the elements inside the div to the left */
  gap: 10px;
  padding: 10px; /* Optional for spacing */
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1); /* Optional for better styling */
}

.menuItem {
  composes: text text_type_main-medium text_color_inactive from global;
  text-decoration: none;
}

.menuItemActive {
  composes: text text_type_main-medium from global;
  text-decoration: none;
  color: inherit;
}


.menuInactive {
  composes: mt-20 text text_type_main-default text_color_inactive from global;
  display: flex; /* Ensure it's treated as a flex container */
  flex-direction: column; /* Optional: for vertical alignment of child elements */
  text-align: left; /* Align text to the left */
  align-items: flex-start; /* Align flex items to the left */
  justify-content: flex-start; /* Align content to the top-left */
}
